import React from 'react'
import "./About.css";

const TextBox = () => {
    return (
        <div className='text-box'>
            <p>Per4mance, nace como una app hbrida, desarrollada mediante el framework de programacin web de JavaScript (Lenguaje de programacin interpretado, es decir, que ejecuta las instrucciones directamente sin necesidad de compilacin en lenguaje mquina), React, que junto a los lenguajes de programacin HTML (Lenguaje de Marcas de Hipertexto) y JavaScript nos permite vincular un SGBD (Sistema Gestor de Bases de Datos) no relacional llamado MongoDB,
                para estructurar los datos de los usuarios en cuanto datos personales se refiere e interactuar con la herramienta que podremos acceder mediante un servidor alojado en un hosting.</p>
            <p>La aplicacin sirve de apoyo para aquellas personas cuya prioridad es mantener una buena salud teniendo en cuenta sus progresos, en cuanto a objetivos, de la vida diaria como son la prdida o ganancia de caloras, la cantidad de agua ingerida o la cantidad de horas de sueo realizadas.</p>
            <p>Se podr disfrutar tanto en dispositivos de alta y baja resolucin para deleite del usuario lo que permite a la aplicacin una disponibilidad completa en cualquier momento.</p>
            <p>Este proyecto es el deseo del desarrollador de crear una app intuitiva y accesible para gestionar de manera clara su progreso personal sin necesidad de pagar una alta cuanta de dinero por un gestor,
            ya que dispondr de su app a 'golpe de dos clicks'.</p>
        </div>
    )
}

export default TextBox